<template>
  <div id="app">
    <section class="section">
      <nav class="navbar is-fixed-top is-info" role="navigation" aria-label="main navigation">
        <div class="navbar-brand">
          <div class="navbar-item">
            <h1 class="subtitle has-text-white">Backend-Less Shortener</h1>
          </div>
          <a role="button" class="navbar-burger burger" aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>

        <div id="navbarBasicExample" class="navbar-menu">
          <div class="navbar-start">
            <router-link class="navbar-item" to="/">Dashboard</router-link>
            <router-link class="navbar-item" to="/create">Create</router-link>
          </div>

          <div class="navbar-end">
            <div class="navbar-item">
                <div class="buttons">
                  <span v-if="authorized">
                      <a v-on:click="logout()" v-bind:href="logOutUrl" class="button is-light">Log Out</a>
                  </span>
                  <span v-else>
                    <a v-bind:href="signUpUrl" class="button is-primary"><strong>Sign up</strong></a>
                    <a v-bind:href="logInUrl" class="button is-light">Log in</a>
                  </span> 
              </div>
            </div>
          </div>
        </div>
      </nav>
    </section>
    <section class="section">
      <div v-if="authorized">
        <div class="container">
          <router-view/>
        </div>
      </div>
      <div v-else>
        Please login
      </div>
    </section>
  </div>

</template>

<script>
import { mapState } from 'vuex';
import axios from 'axios';

// Set Cognito Vars
const clientId = process.env.VUE_APP_CLIENT_ID;
const domainID = process.env.VUE_APP_DOMAIN_ID;
const region = process.env.VUE_APP_REGION;
const queryStringParams = new URLSearchParams(window.location.search);
const cognitoCode = queryStringParams.get('code') || null;
const redUrl = window.location.origin;

export default {
  name: 'app',
  data() {
    return {
      signUpUrl: `https://${domainID}.auth.${region}.amazoncognito.com/signup?response_type=code&client_id=${clientId}&redirect_uri=${redUrl}`,
      logInUrl: `https://${domainID}.auth.${region}.amazoncognito.com/login?response_type=code&client_id=${clientId}&redirect_uri=${redUrl}`,
      logOutUrl: `https://${domainID}.auth.${region}.amazoncognito.com/logout?client_id=${clientId}&logout_uri=${redUrl}`
    }
  },
  created() {
    if (cognitoCode) this.exchangeToken();
    else this.exchangeRefreshToken();
  },
  methods:{
    convertJSON: function(json) {
      const oAuthTokenBodyArray = Object.entries(json).map(([key, value]) => {
        const encodedKey = encodeURIComponent(key)
        const encodedValue = encodeURIComponent(value)
        return `${encodedKey}=${encodedValue}`
      })
      return oAuthTokenBodyArray.join('&')
    },
    exchangeRefreshToken: function(){
      const oauthTokenBodyJson = {
        grant_type: 'refresh_token',
        client_id: clientId,
        refresh_token: localStorage.getItem('cognitoRefreshToken')
      }
      const oauthTokenBody = this.convertJSON(oauthTokenBodyJson)

      return axios.post(`https://${domainID}.auth.${region}.amazoncognito.com/oauth2/token`, oauthTokenBody, {
          ['Content-Type']: 'application/x-www-form-urlencoded'
      }).then(response => {
        let json = response.data;
        if (json.id_token) {
            localStorage.setItem('cognitoIdentityToken', json.id_token);
            this.$store.commit('authorize')
        }
      }).catch(() => {
          this.$store.commit('deAuthorize')
      })

    },
    exchangeToken: function() {
      const oauthTokenBodyJson = {
          grant_type: 'authorization_code',
          client_id: clientId,
          code: cognitoCode,
          redirect_uri: redUrl
      }
      const oauthTokenBody = this.convertJSON(oauthTokenBodyJson)

      return axios.post(`https://${domainID}.auth.${region}.amazoncognito.com/oauth2/token`, oauthTokenBody, {
          ['Content-Type']: 'application/x-www-form-urlencoded'
      }).then(response => {
        let json = response.data;
        if (json.id_token) {
            localStorage.setItem('cognitoIdentityToken', json.id_token);
            localStorage.setItem('cognitoRefreshToken', json.refresh_token);
            this.$store.commit('authorize')
        }
        let query = Object.assign({}, this.$route.query);
        delete query.code;
        this.$router.replace({ query });
      }).catch(() => {
          this.$store.commit('deAuthorize')
      })
    },
    logout: function(){
      localStorage.setItem('cognitoIdentityToken', null);
      localStorage.setItem('cognitoRefreshToken', null);
    }
  },
  computed: {
    ...mapState(['authorized'])
  }
}
</script>
