<template>
  <div class="dashboard">
    <h1 class="title">My links <button class="button" v-on:click="toggleModal('create')">Create</button></h1>
    <table class="table is-fullwidth is-narrow is-striped">
      <thead>
          <tr>
              <th>Short Link</th>
              <th>Url</th>
              <th>Time Stamp</th>
              <th></th>
          </tr>
      </thead>
      <tbody>
          <tr v-for="(link, i) in links" v-bind:key="link.id">
              <td>{{link.id}}</td>
              <td>{{link.url}}</td>
              <td>{{link.timestamp}}</td>
              <td class="has-text-right">
                <button v-on:click="toggleModal('edit', link, i)" class="button is-small is-primary">Update</button>
                <button v-on:click="deleteLink(link.id, i)" class="button is-small is-warning">Delete</button>
                <a class="button is-small is-info" target="_blank" :href="apiUrl + '/' + link.id">Try</a>
              </td>
          </tr>
      </tbody>
    </table>

    <!-- Edit Modal -->
    <div class="modal" v-bind:class="{'is-active': modalIsActive}">
      <div class="modal-background"></div>
        <div class="modal-card">
          <header class="modal-card-head">
            <p class="modal-card-title">
              <span v-if="modalTypeCreate">Create</span>
              <span v-else>Update</span> Shortlink
            </p>
            <button class="delete" v-on:click="toggleModal()" aria-label="close"></button>
          </header>
          <section class="modal-card-body">
            <div class="field">
              <div class="control">
                <input class="input" v-model="model.id" type="text" placeholder="Short Link" required :disabled="!modalTypeCreate">
              </div>
            </div>
            <div class="field">
              <div class="control">
                <input class="input" v-model="model.url" type="text" placeholder="Url (Ex: http://mylink.com)" required>
              </div>
            </div>
          </section>
          <footer class="modal-card-foot">
            <button v-if="modalTypeCreate" v-on:click="createLink()" class="button is-success">Create</button>
            <button v-else v-on:click="updateLink()" class="button is-success">Update</button>
            <button class="button" v-on:click="toggleModal()">Cancel</button>
          </footer>
        </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import axios from 'axios'

export default {
  name: 'dashboard',
  data(){
    return {
      apiUrl: process.env.VUE_APP_API_ROOT,
      modalIsActive: false,
      model:{
        id: "",
        url: ""
      },
      currentLink: {},
      currentIndex: 0,
      modalTypeCreate: true,
    }
  },
  created(){
    this.fetchData();
  },
  computed: {
    ...mapState(['links'])
  },
  methods:{
    toggleModal: function(type, link = null, ind = 0){
      this.modalTypeCreate = type === 'create';
      this.modalIsActive = !this.modalIsActive;

      if(type === 'edit'){
        this.currentLink = link;
        this.currentIndex = ind;
        this.model.id = link.id;
        this.model.url = link.url;
      }
    },
    fetchData: function(){
      axios.get(`${this.apiUrl}/links`, { headers: { Authorization: window.localStorage.getItem('cognitoIdentityToken') } })
        .then(response => (this.$store.commit('hydrateLinks', response.data)))
        .catch(() => (this.$store.commit('drainLinks')))
    },
    createLink: function() {
      let that = this;
        axios.post(`${that.apiUrl}/links`, that.model, { headers: { Authorization: window.localStorage.getItem('cognitoIdentityToken') } })
          .then(response => {
            if(response.data.error){
              alert(response.data.message);
          } else {
              that.toggleModal();
              that.$store.commit('addLink', response.data);
          }
        }).catch(err => { alert(err) })
    },
    updateLink: function() {
      let that = this;
      that.currentLink.url = that.model.url;
        axios.put(`${that.apiUrl}/links/${that.currentLink.id}`, that.currentLink, { headers: { Authorization: window.localStorage.getItem('cognitoIdentityToken') } })
          .then(response => {
            that.toggleModal();
            that.$store.commit('updateLink', response.data, that.currentIndex);
        }).catch(err => { alert(err) })
    },
    deleteLink: function(id, ind){
      if(confirm(`Are you sure you want to delete '${id}'`)){
        let that = this;
        axios.delete(`${that.apiUrl}/links/${id}`, { headers: { Authorization: window.localStorage.getItem('cognitoIdentityToken')}})
          .then(response => {
            if(response.data.success){
              that.$store.commit('removeLink', ind);
            } else {
              alert(response.data.message);
            }
          }).catch(err => { alert(err) })
      }
    }
  }
}
</script>
